body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:black;

}

.Container
{
  max-width:700px;
  margin:auto;
}
.nav
{
  background-color:#007fff;
  height:110px;
  border-radius: 4px;
}
.nav h3
{
padding-top:30px;
padding-left:20px;
 color:whitesmoke;
}
.nav ul{
  padding-left:370px;
display: flex;
}
.nav ul li{
  padding:0px 10px;
  list-style-type: none;
  
 
}
.nav ul li a
{
  color :white;
  text-decoration: none;
  background-color:saddlebrown;
  padding :3px 12px;
  font-weight: bold;
  border-radius: 4px;
}
.nav ul li a:hover
{
  background-color:black;
  color:white;
}
.content
{
  background-color:white ;
  height:100%;
  border-radius:5px;
}

